import { ensureRegion } from "@/api/RegionProvider";
import { ApiHttpClient } from "@/globals";

export default {

    cache: {},

    getString(mid, { region, param, format, okcb, errcb }) {
        region = ensureRegion(region);
        if (!param) param = '';
        let cacheKey = `${mid}:${btoa(param)}//${format}`;
        let cachedRegion = this.cache[region];
        if (cachedRegion) {
            let cachedResult = cachedRegion[cacheKey];
            if (cachedResult) {
                okcb(cachedResult);
                return;
            }
        }

        ApiHttpClient.get(`uistring.php?lan=${region}&t=mid&id=${mid}`,
            {
                params: {
                    fmt: format,
                    param: param,
                }
            }).then(
            (res) => {
                let cachedRegion = this.cache[region];
                if (!cachedRegion) {
                    cachedRegion = this.cache[region] = {};
                }

                cachedRegion[cacheKey] = res.data;
                okcb(res.data);
            }, 
            errcb);
    },

    getVariant(mid, { region, param, okcb, errcb }) {
        region = ensureRegion(region);
        ApiHttpClient.get(`uistring.php?lan=${region}&t=variant&id=${mid}`,
            {
                params: {
                    param: param,
                }
            }).then(
            (res) => {
                okcb(res.data);
            }, 
            errcb);
    },

    getBulk({ page, size, query }, { region, format, okcb, errcb }) {
        region = ensureRegion(region);
        if (query) {
            ApiHttpClient.get(`uistring.php?lan=${region}&t=list`,
            {
                params: {
                    fmt: format,
                    p: page,
                    sz: size,
                    q: query
                }
            }).then(
            (res) => {
                let cachedRegion = this.cache[region];
                if (!cachedRegion) {
                    cachedRegion = this.cache[region] = {};
                }
    
                okcb(res.data);
            }, 
            errcb);
        } else {
            ApiHttpClient.get(`uistring.php?lan=${region}&t=list`,
            {
                params: {
                    fmt: format,
                    p: page,
                    sz: size
                }
            }).then(
            (res) => {
                let cachedRegion = this.cache[region];
                if (!cachedRegion) {
                    cachedRegion = this.cache[region] = {};
                }
    
                okcb(res.data);
            }, 
            errcb);
        }

        
    }



};
